.home {
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: left;
    flex-direction: column;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  
  .headerContainer {
    width: auto;
    margin-left: 30px;
  }
  
  .home .headerContainer h1 {
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 70px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: #fff;
  }
  
  .home .headerContainer p {
    margin-top: 15px;
    padding: 0;
    font-size: 40px;
    font-weight: lighter;
    color: #fff;
  }
  
  .headerContainer button {
    background-color: rgba(188, 0, 10, 255);
    margin-top: 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    height: 50px;
    width: 180px;
    text-decoration: none;
    color: white;
    font-size: 20px;
  }
  
  .headerContainer button:hover {
    background-color: #bc000a;
    cursor: pointer;
    transition: 0.3s ease-in;
  }
  
  /* mobile view  */
  
  @media only screen and (max-width: 600px) {
    .home {
      justify-content: center;
      align-items: center;
    }
    .headerContainer {
      margin-left: 0;
      border-radius: 10px;
      width: 90%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
  
      color: white;
    }
    .home .headerContainer h1 {
      margin-bottom: 30px;
      font-size: 40px;
      height: 30px;
      color: white;
    }
    .headerContainer button {
      margin-top: 40px;
    }
  }